import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Radio
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/trialBalanceDepartmentReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const xx = 15;

class TrialBalanceDepartmentReport extends Component {
  state = {
    filter: {
      gl_type: null,
      id_department: null,
      display_parent: 0,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss")
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getTrialBalanceDepartmentReports } = this.props;
    getTrialBalanceDepartmentReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.trialBalanceDepartmentReport.error) {
      notification("error", this.props.trialBalanceDepartmentReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.trialBalanceDepartmentReport.success) {
      notification("success", this.props.trialBalanceDepartmentReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "display_parent") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value.target.value
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_department) {
      string += "id_department=" + filter.id_department;
    }

    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.gl_type) {
      string
        ? (string += "&gl_type=" + filter.gl_type)
        : (string += "gl_type=" + filter.gl_type);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getTrialBalanceDepartmentReports } = this.props;
    //const { current_page } = this.props.trialBalanceDepartmentReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getTrialBalanceDepartmentReports(1, null, filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getTrialBalanceDepartmentReports } = this.props;
    this.setState({
      filter: {
        gl_type: null,
        id_department: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      }
    });
    this.setState({ search: null });
    getTrialBalanceDepartmentReports();
  };

  hitungTotalAmountParent = (array_ori, array_filter) => {
    array_filter.map(data => {
      if (data.has_child === 1) {
        let filter = array_ori.filter(
          x => x.id_account_parent === data.id_account
        );
        if (filter.length > 0) {
          this.hitungTotalAmountParent(array_ori, filter);
        }
      } else if (data.id_account_parent) {
        let parent = array_ori.find(
          x => x.id_account === data.id_account_parent
        );
        array_ori.map(x => {
          if (x.id_account === parent.id_account) {
            let parent2 = parent;
            while (parent2) {
              parent2.opening_balance_account += parseFloat(
                data.opening_balance_account
              );
              parent2.debit_balance += parseFloat(data.debit_balance);
              parent2.credit_balance += parseFloat(data.credit_balance);
              parent2.ending_balance_account += parseFloat(
                data.ending_balance_account
              );
              parent2 = array_ori.find(
                y => y.id_account === parent2.id_account_parent
              );
              //console.log(parent2)
            }
          }
        });
      }
    });
  };

  modifyData = data => {
    const { display_parent } = this.state.filter;
    let dataSource = [];
    let dataExcel = [];

    // Manipulasi data
    if (data.length > 0) {
      let total_debit = 0;
      let total_credit = 0;

      data.map((trial_balance_report, key) => {
        let currData = Object.assign({}, trial_balance_report);
        currData.key = currData.id_account;

        if (display_parent) {
          if (currData.level > 1 || currData.has_child === 0) {
            currData.ending_balance_account =
              parseFloat(currData.opening_balance_account) +
              parseFloat(currData.debit_balance) +
              parseFloat(currData.credit_balance);

            total_debit += parseFloat(currData.debit_balance);
            total_credit += parseFloat(currData.credit_balance);
          }

          // supaya parent tidak ada amount nya
          if (currData.has_child) {
            currData.opening_balance_account = 0;
            currData.debit_balance = 0;
            currData.credit_balance = 0;
            currData.ending_balance_account = 0;
          }

          // mengembalikan id account parent yang dihilangkan
          if (currData.level === 2 && currData.id_account_parent_bak) {
            currData.id_account_parent = currData.id_account_parent_bak;
          }

          dataSource.push(currData);
        } else {
          if (currData.level > 1 || currData.has_child === 0) {
            currData.ending_balance_account =
              parseFloat(currData.opening_balance_account) +
              parseFloat(currData.debit_balance) +
              parseFloat(currData.credit_balance);

            total_debit += parseFloat(currData.debit_balance);
            total_credit += parseFloat(currData.credit_balance);
            dataSource.push(currData);
          }

          // supaya parent tidak ada amount nya
          if (currData.has_child) {
            currData.opening_balance_account = 0;
            currData.debit_balance = 0;
            currData.credit_balance = 0;
            currData.ending_balance_account = 0;
          }

          // supaya id account parent di level 2 null, jadi bisa diitung
          if (currData.level === 2 && currData.id_account_parent) {
            currData.id_account_parent_bak = currData.id_account_parent;
            currData.id_account_parent = null;
          }
        }
      });

      if (this.state.filter.display_parent) {
        let level_1 = dataSource.filter(x => x.level === 1);
        this.hitungTotalAmountParent(dataSource, level_1);
      } else {
        let level_2 = dataSource.filter(x => x.level === 2);
        this.hitungTotalAmountParent(dataSource, level_2);
      }

      const total = {
        key: "Total",
        account_code: "Total",
        debit_balance: total_debit,
        credit_balance: total_credit
      };

      dataSource.push(total);
    }

    if (this.state.filter.display_parent) {
      dataSource.map(trial_balance_report => {
        let row = Object.assign({}, trial_balance_report);
        dataExcel.push(
          Object.assign({}, row, {
            account_code:
              row.level > 1
                ? " ".repeat(row.level * 3) + row.account_code
                : row.account_code,
            account_name:
              row.level > 1
                ? " ".repeat(row.level * 3) + row.account_name
                : row.account_name
          })
        );
      });
    } else {
      dataExcel = dataSource;
    }

    return { dataSource, dataExcel };
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: "Account Code",
      dataIndex: "account_code",
      rowKey: "account_code",
      width: "10%",
      spanCheck: "string",
      spanCheckValue: "Total",
      spanValue: 3,
      render: (text, row) => {
        if (text === "Total") {
          return {
            children: <strong style={{ marginLeft: "50%" }}>{text}</strong>,
            props: {
              colSpan: 3
            }
          };
        }

        if (this.state.filter.display_parent) {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          } else if (row.level === 1) {
            return text;
          } else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }

        return text;
      }
    },
    {
      title: "Account Name",
      dataIndex: "account_name",
      rowKey: "account_name",
      width: "20%",
      spanCheck: "string",
      spanCheckValue: "Total",
      spanValue: 0,
      render: (text, row) => {
        if (row.account_code === "Total") {
          return {
            props: {
              colSpan: 0
            }
          };
        }

        if (this.state.filter.display_parent) {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          } else if (row.level === 1) {
            return text;
          } else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }

        return text;
      }
    },
    {
      title: "Beginning Balance (IDR)",
      dataIndex: "opening_balance_account",
      rowKey: "opening_balance_account",
      align: "right",
      width: "20%",
      parse: true,
      spanCheck: "string",
      spanCheckValue: "Total",
      spanValue: 0,
      render: (text, row) => {
        if (row.account_code === "Total") {
          return {
            props: {
              colSpan: 0
            }
          };
        }

        // if (this.state.filter.display_parent) {
        //   if (row.has_child) {
        //     if (row.level > 1) {
        //       return null;
        //     } else {
        //       return null;
        //     }
        //   }
        // }

        const str = text
          ? <NumberFormat value={parseFloat(text)} />
          : "0";

        if (row.has_child && this.state.filter.display_parent) {
          return <strong>{str}</strong>;
        } else {
          return str;
        }
      }
    },
    {
      title: "Changes",
      children: [
        {
          title: "Total Debit (IDR)",
          dataIndex: "debit_balance",
          rowKey: "debit_balance",
          align: "right",
          width: "15%",
          parse: true,
          spanCheck: "string",
          spanCheckValue: "Total",
          spanValue: 1,
          render: (text, row) => {
            if (row.account_code === "Total") {
              return {
                children: (
                  <strong>
                    <NumberFormat value={parseFloat(text)} />
                  </strong>
                ),
                props: {
                  colSpan: 1
                }
              };
            }

            // if (this.state.filter.display_parent) {
            //   if (row.has_child) {
            //     if (row.level > 1) {
            //       return null;
            //     } else {
            //       return null;
            //     }
            //   }
            // }

            const str = text
              ? <NumberFormat value={parseFloat(text)} />
              : "0";

            if (row.has_child && this.state.filter.display_parent) {
              return <strong>{str}</strong>;
            } else {
              return str;
            }
          }
        },
        {
          title: "Total Credit (IDR)",
          dataIndex: "credit_balance",
          rowKey: "credit_balance",
          align: "right",
          width: "15%",
          parse: true,
          spanCheck: "string",
          spanCheckValue: "Total",
          spanValue: 1,
          render: (text, row) => {
            if (row.account_code === "Total") {
              return {
                children: (
                  <strong>
                    <NumberFormat value={parseFloat(text)} />
                  </strong>
                ),
                props: {
                  colSpan: 1
                }
              };
            }

            // if (this.state.filter.display_parent) {
            //   if (row.has_child) {
            //     if (row.level > 1) {
            //       return null;
            //     } else {
            //       return null;
            //     }
            //   }
            // }

            const str = text
              ? <NumberFormat value={parseFloat(text)} />
              : "0";

            if (row.has_child && this.state.filter.display_parent) {
              return <strong>{str}</strong>;
            } else {
              return str;
            }
          }
        }
      ]
    },
    {
      title: "Ending Balance (IDR)",
      dataIndex: "ending_balance_account",
      rowKey: "ending_balance_account",
      width: "20%",
      align: "right",
      parse: true,
      spanCheck: "string",
      spanCheckValue: "Total",
      spanValue: 1,
      render: (text, row) => {
        if (row.account_code === "Total") {
          return null;
        }

        // if (this.state.filter.display_parent) {
        //   if (row.has_child) {
        //     if (row.level > 1) {
        //       return null;
        //     } else {
        //       return null;
        //     }
        //   }
        // }

        const str = text
          ? <NumberFormat value={parseFloat(text)} />
          : "0";

        if (row.has_child && this.state.filter.display_parent) {
          return <strong>{str}</strong>;
        } else {
          return str;
        }

        // return (
        //   parseFloat(row.opening_balance_account) +
        //   parseFloat(row.debit_balance) +
        //   parseFloat(row.credit_balance)
        // )
        //   .toFixed(2)
        //   .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
    }
  ];

  render() {
    // console.log(this.props.trialBalanceDepartmentReport);
    // const { display_parent } = this.state.filter;
    // Data setup
    const {
      trial_balance_department_reports,
      locations,
      departments,
      gl_types,
      loading,
      filter
    } = this.props.trialBalanceDepartmentReport
      ? this.props.trialBalanceDepartmentReport
      : [];
    //console.log(this.props.trialBalanceDepartmentReport.base_currency); //console.log(this.state);
    const modifiedData = this.modifyData(trial_balance_department_reports);

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={
              <IntlMessages id="trialBalanceDepartmentReports.pageHeader" />
            }
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="trialBalanceDepartmentReports.pageHeader" />
              }
              // scale={0.9}
              columns={this.columns}
              data={modifiedData.dataSource}
              filter={this.state.filter}
              departments={departments}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
            />

            <ExcelWrapper
              title="trialBalanceDepartmentReports.pageHeader"
              fileName="trial_balance_department_report"
              loading={loading}
              columns={this.columns}
              data={modifiedData.dataExcel}
              filter={this.state.filter}
              locations={locations}
              departments={departments}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="departments.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_department"
                      )}
                      value={
                        this.state.filter.id_department
                          ? this.state.filter.id_department
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.department" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {`${data.department_code} (${data.department_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="generalLedgers.type" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "gl_type")}
                      value={
                        this.state.filter.gl_type
                          ? this.state.filter.gl_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="generalLedgers.type.select" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {gl_types.map((data, key) => (
                        <Option key={key} value={data.gl_type}>
                          {`${data.description}`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>

                <Row>
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={8}>
                        <label>Display Parent</label>
                        <br />
                        <Radio.Group
                          onChange={this.handleFilterChange.bind(
                            this,
                            "display_parent"
                          )}
                          value={
                            this.state.filter.display_parent
                              ? this.state.filter.display_parent
                              : 0
                          }
                          buttonStyle="solid"
                        >
                          <Radio.Button value={0}>
                            <IntlMessages id="no" />
                          </Radio.Button>
                          <Radio.Button value={1}>
                            <IntlMessages id="yes" />
                          </Radio.Button>
                        </Radio.Group>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                          disabled={!this.state.filter.id_department}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noDepartment" />} />
            ) : !filter.includes("id_department=") ? (
              <Empty description={<IntlMessages id="noDepartment" />} />
            ) : trial_balance_department_reports.length === 0 &&
              !this.props.trialBalanceDepartmentReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="trialBalanceDepartmentReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={modifiedData.dataSource}
                    columns={this.columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    trialBalanceDepartmentReport: state.trialBalanceDepartmentReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(TrialBalanceDepartmentReport);
